}
.sht_box_1 li:first-child,
.sht_box_2 li:first-child,
.sht_box_3 li:first-child,
.sht_box_4 li:first-child {
  height: 50px;
  font-weight: bold;
  font-size: 18px;
}
.sht_box_1 li,
.sht_box_2 li,
.sht_box_3 li,
.sht_box_4 li {
  width: 100%;
  height: 35px;
  line-height: 35px;
}
.sht_box_1 li:hover a,
.sht_box_2 li:hover a,
.sht_box_3 li:hover a,
.sht_box_4 li:hover a {
  color: #002053;
